import React, { Component } from 'react';
import { loginUser } from '../../redux/actions/userActions';
import { connect } from 'react-redux';

const inputStyle = {
    padding: '24px 24px 24px 24px', outline: 'none !important', fontFamily: `''Montserrat', sans-serif`,
    boxShadow: 'none !important', fontSize: '16px', marginTop: '12px'
};

const btnStyle = { borderRadius: '20px', marginTop: '16px' };

class index extends Component {
    constructor(props) {
        super(props);
        this.handleInput = this.handleInput.bind(this);
    }
    state = { email: '', password: '' };

    handleInput(event) { this.setState({ [event.target.name]: event.target.value }); }

    handleSubmit(event) {
        const { email, password } = this.state;
        const { loginUser } = this.props;
        event.preventDefault();
        if (email && password) {
            var data = { email: email, password: password };
            loginUser(data);
        }
    }

       //Displays a loader until user authenticats
    renderLoader() {
        const { isLoading } = this.props;
        if (isLoading) return <div className="spinner-border" style={{ width: '1rem', height: '1rem' }} role="status"><span className="sr-only">Loading...</span> </div>
    }

    render() {
        const { email, password } = this.state;
        const { err, isError } = this.props;
        return (
            <form className="col-lg-5 col-11" onSubmit={(e) => this.handleSubmit(e)}>
                <div className="input-group mb-3" style={{ marginTop: '12px' }}>
                    <input type="text" className="form-control" style={inputStyle} name='email' value={email}
                        placeholder="Your work email" onChange={this.handleInput} required />
                </div>
                <div className="input-group mb-3" style={{ marginTop: '12px' }}>
                    <input type="password" className="form-control" name='password' value={password} style={inputStyle}
                        onChange={this.handleInput} placeholder="Your password" required />
                </div>
        <button className="btn btn-block btn-login" style={btnStyle} type='submit'>Login {this.renderLoader()}</button>
                {isError && <h6 style={{ width: '100%', textAlign: 'center', marginTop: '16px', color: 'red', fontSize: '13px !important',fontWeight:'500' }}>
                    {err}
                </h6>}
            </form>
        )
    }
}

const mapStateToProps = state => {
    return {
        isError: state.Login.isError,
        isLoading: state.Login.isLoading,
        err: state.Login.data
    }
}
export default connect(mapStateToProps, { loginUser })(index);