import { userConstants } from '../constants';
const { LErr, LReq, LSuc, Current_User_Err, Current_User_Suc } = userConstants;
export const loginReducer = (state = { isLoading: false, isSuccess: false, isError: false }, action) => {
    switch (action.type) {
        case LReq: return { ...state, isLoading: true, isError: false, isSuccess: false };
        case LSuc: return { ...state, isSuccess: true, isLoading: false, isError: false, data: action.payload };
        case LErr: return { ...state, isError: true, isLoading: false, isSuccess: false, data: action.payload };
        default: return state;
    }
};

export const CurrentUser = (state = { auth: false }, action) => {
    switch (action.type) {
        case Current_User_Err: return { ...state, auth: false }
        case Current_User_Suc: return { ...state, auth: true }
        default: return state
    }
};