import React, { Component } from 'react';
import  LoginCard from '../../components/loginForm';
import './style.css';

const navStyle = { display: 'flex', flexDirection: 'column', alignItems: 'center', marginLeft: 'auto', marginRight: 'auto' };
const logoName = { fontSize: '38px', fontWeight: 'bold', color: '#40739e', fontFamily: `'Montserrat', sans-serif` };
const FormSetting = { display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'flex-start' }

class index extends Component {
    state = {};

    render() {
        return (
            <div className="container-fluid justify-content-lg-center justify-content-start p-0 log-form">
                {LoginNav()}
                {FormSpace()}
            </div>
        )
    }
}

function LoginNav() {
    return <div className="col-11 p-0" style={navStyle}>
        <h6 style={logoName}>School System</h6>
    </div>
}

function FormSpace() {
    return <div className="col-12" style={FormSetting}>
        <LoginCard />
    </div>
}

export default index;